import Header from "@/components/common/Header";
import CellDivider from "@/components/common/CellDivider";

export default {
  name: "",
  props: [""],
  data() {
    return {
      TitleObjData: {
        titleContent: "通证",
        showLeftBack: true,
        showRightMore: false
      },
      cellListTools: [
        // {
